html,
body,
#root,.page {
  height: 100%; /* needed for proper layout */
}

body {
  overflow: hidden;
}

p {
  margin-bottom: 1em;
}

.page {
  display: flex;
  flex-direction: column;
}

.page-header {
  flex: 0 0 auto;
}

.page-content {
  flex: 1 1 auto;
  position: relative;
  overflow-y: auto;
}

.page-footer {
  flex: 0 0 auto;
}
.w-10 {
  width: 10%;
}
.center-section {
  width: 100%;
  max-width: 500px;
  padding: 15px;
  margin: auto;
}